.inquiry {
    background-color: $color-primary;
}

.inquiry-form {
    padding: 0 0 65px 0;
    font-weight: 700;

    @include mq('tablet') {
        padding: 0 0 90px 0;
        width: 90%;
        margin: 0 auto;
    }

    @include mq('bootstrap-lg') {
        width: 80%;
    }

    &--confirmation {

        .inquiry-form__label,
        .inquiry-form__value {
            color: $color-black !important;
        }

        @include mq('bootstrap-lg') {
            width: 80%;
        }
    }

    &__title {
        font-size: 16px;
        line-height: 1.5;
        font-weight: 700;
        text-align: center;
        color: $color-white;
        margin: 0 0 30px;

        @include mq('tablet') {
            margin: 0 0 60px;
            font-size: 18px;
        }
    }

    .form-group {
        margin-bottom: 25px;

        @include mq('tablet') {
            margin-bottom: 40px;
        }
    }

    &__label-wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 10px;

        @include mq('tablet') {
            // justify-content: flex-end;
            margin-bottom: 15px;
            height: 100%;
            margin-bottom: 0;
        }
    }

    &__label {
        // margin-right: 15px;
    }

    &__label,
    &__value {
        font-size: 16px;
        color: $color-white;
        margin-bottom: 0;

        @include mq('tablet') {
            font-size: 16px;
        }

        @include mq('tablet-wide') {
            font-size: 18px;
        }


    }

    &__value {
        font-weight: 400;
    }

    &__state {
        font-size: 14px;
        height: 24px;
        line-height: 1.7;
        line-height: 24px;
        padding: 0 6px;
        background: $color-red;
        color: $color-white;

        @include mq('tablet') {
            font-size: 16px;
            height: 26px;
            line-height: 26px;
            padding: 0 8px;
        }

        &--any {
            background: $color-white;
            color: $color-black;
            border: 1px solid $color-gray-dark--2;
        }
    }

    .form-control {
        height: 50px;
        border-radius: 0;
        border: solid 2px $color-gray-dark--2;
        font-family: $font-primary;
        font-size: 17px;
        font-weight: 400;
        box-shadow: inset 0 1px #afafaf;

        &:focus {
            box-shadow: 0 0 0 1px rgba($color-black, 0.4);
        }

        &::placeholder {
            color: $color-gray-dark--2;
        }
    }

    &__radio-wrapper {

        .custom-control {
            padding-left: 44px;

            @include mq('phablet') {
                padding-left: 2.8rem;
            }

            @include mq('tablet') {
                padding-left: 2.8rem;
            }

            @include mq('tablet-wide') {
                padding-left: 60px;
            }

            &:first-child {
                padding-left: 2.8rem;
            }
        }

        .custom-control-label {
            font-size: 18px;
            color: $color-white;
            display: block;

            &:before,
            &:after {

                left: - 1.8rem;
                top: 0.1rem;
                width: 1.3rem;
                height: 1.3rem;
            }
        }

        .custom-control-label::before {
            border: 2px solid $color-gray-dark--2;
        }

        .custom-control-input:focus:not(:checked)~.custom-control-label::before {
            border-color: $color-white;
        }

        .custom-control-input:focus~.custom-control-label::before {
            box-shadow: 0 0 0 0.2rem rgba($color-primary, 0.3);
        }

        .custom-control-input:checked~.custom-control-label::before {
            border-color: $color-white;
            background-color: $color-primary;
        }

        //reveal if active
        .reveal-if-active {
            opacity: 0;
            max-height: 0;
            overflow: hidden;
            transform: scale(0.8);
            transition: 0.5s;
        }

        .custom-control-input#radio1:checked~.reveal-if-active {
            opacity: 1;
            max-height: 100px;
            overflow: visible;
            transform: scale(1);
            margin-top: 10px;
        }
    }

    .custom-select {
        background: url("../img/arrow-down.svg") no-repeat right 0.75rem center;
        background-size: 22px 22px;
    }

    textarea.form-control {
        height: 250px;
        resize: none;
    }

    &__btn-wrapper {
        margin-top: 45px;
        text-align: center;

        @include mq('tablet') {
            margin-top: 75px;
        }
    }

    &__btn {
        background-color: $color-accent;
        box-shadow: 1px 1px 0px 1px rgba(51, 51, 51, 0.5);
        color: $color-white;
        margin: 0 auto;
        width: 100%;
        height: 70px;
        font-size: 24px;
        font-weight: bold;
        -webkit-appearance: button;
        cursor: pointer;

        @include mq('phablet') {
            width: 370px;
        }

        &:hover {
            text-decoration: none;
            color: $color-white;

            @include mq('tablet') {
                background: rgba($color-accent, 0.8);
            }
        }
    }

    .inquiry-form__privacy {
        font-weight: 400;
        padding: 25px 10px;
        max-height: 200px;
        overflow-y: scroll;
        border: 1px solid #ccc;

        @include mq('tablet') {
            padding: 25px 20px;
        }

        h3 {
            font-size: 20px;
            font-weight: 700;
            padding-bottom: 30px;
            color: $color-white;
        }

        p,
        li,
        h5 {
            font-size: 14px;
            line-height: 1.6;
            font-weight: 500;
            color: $color-white;
        }

        h4 {
            font-size: 16px;
            font-weight: 700;
            line-height: 1.8;
            margin: 25px 0 10px;
            color: $color-white;
        }

        p,
        li {
            padding-left: 15px;
        }
    }
}



// send.php
.form-sended {
    &__wrapper {
        min-height: 60vh;
        text-align: center;
    }

    &__text {
        font-size: 20px;
    }
}




//radio-buttom
.custom-radio .custom-control-label::before {
    border-radius: 0;
}


.err {
    color: $color-red;
}