.voice {
    // background-color: $color-primary;

    &--ex {
        background-color: $color-white;
    }

    @include mq('tablet') {}

    &__row {
        justify-content: center;

        .col-sm-6 {
            @include mq('tablet', 'max') {
                margin-bottom: 15px;
            }
        }

        @include mq('tablet') {
            margin: 0 -15px;

            &>[class^="col"] {
                padding: 0 10px;
            }
        }

        @include mq('tablet-wide') {
            margin: 0 -25px;

            &>[class^="col"] {
                padding: 0 25px;
            }
        }
    }
}


.voice-card {
    padding: 10px;
    border: 1px solid $color-primary;
    // border-radius: 5px;
    // margin-bottom: 35px;
    // background-color: $color-white;
    height: 100%;

    &--bg {
        background-color: $color-white;
    }


    @include mq('phablet') {
        margin-bottom: 15px;
        // padding: 10px;
        // height: 345px;
    }

    @include mq('tablet') {
        margin-bottom: 0;
        // padding: 10px;
        // height: 370px;
    }

    @include mq('tablet-wide') {
        margin-bottom: 0;
        padding: 15px;
        // height: 490px;
    }

    @include mq('tablet', 'max') {
        // height: 95%; 
    }

    &__header {
        text-align: center;
        font-size: 18px;

        img {
            height: 150px;

            @include mq('phablet') {
                width: 180px;
                height: auto;
            }

            @include mq('tablet') {
                height: auto;
            }

            @include mq('tablet-wide') {
                width: 165px;
            }
        }

        &--bg {
            img {
                width: 100%;
            }
        }
    }

    &__body {

        @include mq('phablet') {
            padding: 0;
        }
    }

    &__title {
        font-size: 18px;
        font-weight: 700;
        line-height: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        height: auto;
        margin: 7px 0;
        color: $color-black;



        @include mq('phablet') {
            font-size: 14px;
            // margin: 0 0 15px;
        }

        @include mq('tablet') {
            font-size: 14px;
            margin: 20px 0 25px;
        }

        @include mq('tablet-wide') {
            font-size: 18px;
        }
    }

    &__text {
        font-size: 16px;
        line-height: 1.8;
        margin: 0;
        font-weight: 400;
        color: $color-black;



        @include mq('phablet') {
            font-size: 13px;
        }

        @include mq('tablet') {
            font-size: 12px;
            padding-top: 15px;
        }

        @include mq('tablet-wide') {
            font-size: 16px;
        }
    }
}