//SECTION FLOW
.flow {
    background-color: $color-primary;

    @include mq('phablet') {
        margin: 0;
        width: auto;
    }

    @include mq('tablet-wide') {
        // padding-top: 25px;
    }


    &__row {

        @include mq('bootstrap-lg') {
            margin: 0 -22px;

            &>[class^="col"] {
                padding: 0 22px;
            }
        }
    }
}

.flow-card {
    margin-bottom: 60px;

    @include mq('phablet') {
        margin-bottom: 35px;
    }

    @include mq('tablet-wide') {
        margin-bottom: 0;
    }

    &--triangle {
        position: relative;

        &:before {
            content: '';
            position: absolute;
            bottom: -45px;
            right: 50%;
            transform: translateX(50%) rotate(90deg);
            width: 0;
            height: 0;
            border-top: 12px solid transparent;
            border-left: 15px solid $color-white;
            border-bottom: 12px solid transparent;

            @include mq('phablet') {
                top: 75px;
                right: -16px;
                transform: translateX(0) rotate(0deg);
            }

            @include mq('bootstrap-lg') {
                right: -30px;
            }
        }
    }


    &__header {
        text-align: center;
        // position: relative;
        padding-top: 50px;
        font-size: 18px;
        margin: 0 auto;
        width: 80%;

        @include mq('phablet') {
            padding-top: 50px;
        }

        @include mq('tablet') {
            width: auto;
        }

        >img {
            height: 85px;
        }
    }

    &__number {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        background: $color-white;
        color: $color-primary;
        font-weight: bold;
        font-size: 20px;
        width: 37px;
        height: 37px;
        line-height: 37px;
        text-align: center;

        @include mq('tablet') {
            left: 0;
            transform: translateX(0);
        }

    }

    &__title {
        font-size: 18px;
        font-weight: 700;
        line-height: 1.5;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        margin: 15px 0;
        color: $color-white;

        @include mq('phablet') {
            height: 55px;
            margin: 10px 0;
        }
    }

    &__text {
        font-size: 16px;
        text-align: center;
        line-height: 1.8;
        font-weight: 400;
        color: $color-white;

        @include mq('bootstrap-lg') {
            text-align: left;
        }
    }
}