.problem {
    background-color: $color-primary;
    position: relative;

    @include mq('tablet') {
        // padding: 60px 0 80px;
    }

    &__inner {
        @include mq('tablet') {
            background-image: url(../img/problem/problem-bg.png);
            background-size: 50%;
            background-position: right;
            background-repeat: no-repeat;
        }
    }



    &__list {
        @extend %reset-ul;
    }

    &__item {
        margin-bottom: 15px;
        font-size: 15px;
        font-weight: 700;
        color: $color-white;
        // text-shadow: white 1px 1px 2px, white -1px 1px 2px, white 1px -1px 2px, white -1px -1px 2px;
        line-height: 2;
        position: relative;
        padding-left: 30px;

        @include mq('tablet') {
            font-size: 20px;
            padding-left: 45px;
            margin-bottom: 22px;
        }

        @include mq('tablet-wide') {
            font-size: 19px;
        }

        &:last-child {
            margin-bottom: 0;
        }

        img {
            position: absolute;
            width: 24px;
            height: 24px;
            top: 4px;
            left: 0;


            @include mq('tablet') {
                width: 25px;
                height: 25px;
                top: 7px;
            }

            @include mq('tablet') {
                top: 5px;
            }
        }

    }
}