.example {

    &__body {
        border: 1px solid $color-primary;
        text-align: center;
        padding: 30px 25px;
        max-width: 405px;
        margin-left: auto;
        height: 100%;

        @include mq('tablet') {
            padding: 20px 10px;
        }

        @include mq('tablet-wide') {
            padding: 30px 25px;
        }
    }

    &__title {
        font-size: 18px;
        font-weight: 700;
        // display: inline-block;
        color: $color-primary;
        padding-bottom: 15px;
        border-bottom: 1px solid $color-primary;

        @include mq('tablet') {
            font-size: 18px;
            padding-bottom: 15px;
        }

        @include mq('tablet-wide') {
            font-size: 20px;
            padding-bottom: 23px;
        }
    }

    &__desc {
        padding-top: 15px;
        font-size: 14px;
        font-weight: 400;
        text-align: left;
        line-height: 1.6;


        @include mq('tablet') {
            font-size: 14px;
            padding-top: 25px;
        }

        @include mq('tablet-wide') {
            font-size: 16px;
        }

        span {
            font-size: 18px;
            font-weight: 700;
            display: block;
            padding-bottom: 5px;

            @include mq('tablet') {
                font-size: 16px;
            }

            @include mq('tablet-wide') {
                font-size: 18px;
            }
        }
    }

    &__desc-sm {
        font-size: 18px;
        font-weight: 700;
        line-height: 2;
        padding: 15px 0 10px;

        @include mq('tablet') {
            font-size: 16px;
            padding: 25px 0 10px;
        }

        @include mq('tablet-wide') {
            font-size: 18px;
        }

        span {
            display: block;
            font-size: 16px;

            @include mq('tablet') {
                font-size: 14px;
            }

            @include mq('tablet-wide') {
                font-size: 16px;
            }
        }
    }
}