.development {
    &__desc {
        font-size: 15px;
        margin: 15px 0 20px;

        @include mq('tablet') {
            font-size: 18px;
            font-weight: 700;
            margin: 35px 0 50px;
            text-align: center;
        }
    }

    &__title {
        font-size: 18px;
        font-weight: 700;
        color: $color-primary;
        padding-bottom: 20px;
    }

    &__sm-desc {
        font-size: 14px;
        font-weight: 400;
        line-height: 1.6;

        @include mq('tablet') {
            font-size: 16px;
        }
    }
}