.contact-us {


    &__wrapper {
        display: flex;
        text-align: center;
        background-image: url(../img/contact-us/cta-bg.png);
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        position: relative;


        @include mq('tablet') {
            background-image: url(../img/contact-us/cta-bg.png);
            padding: 60px 15px;
            background-size: cover;
        }

        @include mq('tablet', 'max') {
            &:before {
                position: absolute;
                content: '';
                width: 100%;
                height: 100%;
                background-color: rgba(255, 255, 255, .5);
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
            }
        }

    }

    &__inner {
        // width: 100%;
        position: relative;
        width: 100%;
        margin: 0 auto;
        padding: 20px 0;

        @include mq('phablet') {
            width: 95%;
            margin: 0 auto;
            padding: 20px 10px;
        }

        @include mq('tablet') {
            background-color: rgba($color-white, .9);
            width: 95%;
            margin: 0 auto;
            padding: 20px;
        }

        @include mq('tablet-wide') {
            width: 80%;
            margin: 0 auto;
            padding: 30px;
        }

        @include mq('desktop') {
            width: 72%;

        }
    }


    &__title {
        font-size: 20px;
        font-weight: 900;
        color: $color-primary;
        text-align: center;
        line-height: 1.2;


        @include mq('tablet-small') {
            font-size: 25px;
        }

        @include mq('tablet') {
            font-size: 30px;
        }

        @include mq('tablet-wide') {
            font-size: 39px;
        }

        span {


            font-size: 16px;

            @include mq('tablet') {
                font-size: 20px;
            }

            @include mq('tablet-wide') {
                font-size: 29px;
            }
        }
    }


    &__btn {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        margin: 10px auto 0;

        @include mq('phablet') {
            margin: 20px 0 0;
            // justify-content: space-between;
            // flex-wrap: nowrap;
        }

        @include mq('tablet') {
            flex-wrap: nowrap;
            justify-content: space-between;
        }



        .btn-phone {
            img {
                width: 20px;
                margin-right: 5px;

                @include mq ('phablet') {
                    width: 20px;
                }

                @include mq ('tablet') {
                    width: 22px;
                }
            }

        }

        //consulation
        .consultation {

            @include mq('tablet') {
                padding: 7px 8px !important;
            }

            .consultation__text-small {
                font-size: 14px;

                @include mq('tablet-small') {
                    font-size: 14px;
                }

                @include mq('tablet') {
                    font-size: 15px;
                }
            }

            .consultation__text-big {
                font-size: 20px;

                @include mq('tablet-small') {
                    font-size: 20px;
                }

                @include mq('tablet') {
                    font-size: 23px;
                }
            }

        }

        //btn-phone
        .btn-phone {
            align-self: center;

            .btn-phone__time {
                font-size: 14px;

                @include mq('tablet-small') {
                    font-size: 15px;
                }

                @include mq('tablet') {
                    font-size: 17px;
                }
            }

            .btn-phone-top {
                font-size: 28px;

                @include mq('tablet-small') {
                    font-size: 30px;
                }

                @include mq('tablet') {
                    font-size: 35px;
                }
            }
        }


        //btn-mail

        .btn-mail {
            margin-top: 10px;
            margin-left: 0;
            padding: 10px;

            @include mq('tablet') {
                margin-left: 15px;
                margin-top: 0;
                padding: 10px 15px;
            }

            @include mq('tablet-wide') {

                padding: 15px;
            }


            .btn-mail-text-top {
                font-size: 20px;
            }

            .btn-mail-text-btm {
                font-size: 15px;
            }
        }
    }
}