.data-entry {
    background-color: $color-primary;

    &__desc {
        font-size: 16px;
        line-height: 1.6;
        text-align: center;
        color: $color-white;
        font-weight: 700;
        padding-top: 15px;

        @include mq('tablet') {
            font-size: 19px;
        }
    }

    &__row {
        margin-top: 0;

        @include mq('tablet') {
            margin-top: 40px;
        }
    }

    &__col {
        background-color: $color-white;
        width: 160px;
        height: 160px;
        margin: 0 auto 15px;
        border-radius: 50%;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;

        @include mq('tablet') {
            width: 160px;
            height: 160px;
            margin-bottom: 0;
        }

        @include mq('tablet-wide') {
            width: 185px;
            height: 185px;
        }

        &--color {
            background-color: #ccc;
        }

        img {
            max-width: 85px;
            max-height: 80px;
            margin: 0 auto;
        }

        figcaption {
            font-size: 16px;
            font-weight: 700;
            line-height: 1.5;
            text-align: center;
            color: $color-primary;
            margin-top: 10px;

            @include mq('tablet') {
                font-size: 16px;
            }

            @include mq('tablet-wide') {
                font-size: 18px;
            }
        }
    }
}