 .hero {



     background-image: url(../img/hero/hero-pc.png);
     background-repeat: no-repeat;
     background-position: center;
     background-size: 100% 100%;
     //  background-color: rgba($color-primary, .2);
     padding: 30px 0 30px;
     //  height: 75vh;

     &__box {
         display: flex;
     }

     @include mq('tablet') {
         background-image: url(../img/hero/hero-pc.png);
         background-repeat: no-repeat;
         background-position: center right;
         background-size: cover;
         height: 100%;
         padding: 50px 0 30px;
     }


     &__details {
         display: block;

         width: 100%;

         @include mq('phablet') {
             padding: 0 0 5px;
             width: 100%;
         }

         @include mq('tablet') {
             width: 67%;
             margin: 0 auto;
             //  padding-left: 45px;
             //  padding-bottom: 45px;
         }
     }




 }