//SECTION PROBLEM
.reason {}

// reason card
.reason-card {
    margin-bottom: 40px;

    @include mq('tablet-wide') {
        margin-bottom: 0;
    }

    &__header {
        text-align: center;
        position: relative;
        font-size: 18px;
        margin: 0 auto;
        // width: 80%;

        @include mq('tablet') {
            width: auto;
        }
    }

    &__number {
        position: absolute;
        top: 0;
        left: 0;
        background: $color-primary;
        color: $color-white;
        font-size: 20px;
        font-weight: bold;
        width: 40px;
        height: 40px;
        line-height: 40px;
        text-align: center;
    }


    &__body {
        padding: 0 10px;

        @include mq('phablet') {
            padding: 0;
        }
    }

    &__title {
        font-size: 16px;
        font-weight: 700;
        line-height: 1.7;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        height: auto;
        // margin: 15px 0;


        @include mq('tablet') {
            font-size: 18px;
            // margin: 20px 0;
            height: 90px;
        }

    }

    &__text {
        font-size: 14px;
        text-align: justify;
        line-height: 1.8;
        margin: 0;
        font-weight: 400;

        @include mq('tablet') {
            font-size: 16px;
        }
    }
}