.header {

    // padding: 0 15px;
    position: relative;
    border-top: 4px solid $color-primary;

    &__top {

        // @include mq("tablet") {
        //    
        // }
    }

    &__tagline {
        font-weight: normal;
        font-size: 10px;
        font-weight: 700;
        line-height: 17px;
        padding: 8px 0 14px;
        color: $color-black;

        @include mq("tablet") {
            font-size: 14px;
            padding: 15px 0 19px;
        }
    }

    &__bottom {
        padding-bottom: 10px;

        @include mq("tablet") {
            padding-bottom: 20px;
        }
    }

    &__flex-container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: row;
    }

    &__logo {
        width: 250px;

        // @include mq('phablet') {
        //     width: 158px;
        // }

        @include mq("tablet") {
            width: 200px;
        }

        @include mq("tablet-wide") {
            width: 250px;
        }
    }

    &__btn-group {
        display: none;
        justify-content: center;
        align-items: center;
        margin-top: 15px;

        @include mq("tablet") {
            display: flex;
            margin-top: 0;
            margin-left: 15px;
            align-items: center;
        }
    }
}