.footer {
    background: $color-primary;
    color: $color-white;
    text-align: center;
    margin-bottom: 65px;
    border-top: 1px solid $color-primary;

    @include mq('phone-wide') {
        margin-bottom: 75px;
    }

    @include mq('phablet') {
        margin-bottom: 0;
    }

    &__top {
        background-color: $color-white;
        color: $color-white;
        padding: 15px;

        @include mq('tablet') {

            padding: 35px 25px;
        }
    }



    &__address {
        line-height: 1;
        text-align: center;
        margin-top: 20px;
        color: $color-black;


        p {
            // margin-bottom: 20px;
            line-height: 1.8;
            font-size: 12px;
            font-weight: 400;
            color: #333;


            @include mq('tablet') {
                font-size: 16px;
            }

            span {
                font-size: 18px;
                font-weight: 700;
            }
        }
    }

    &__logo {
        width: 250px;
        margin: 0 auto;
    }


    &__copyright {
        padding: 20px 0;
        font-size: 14px;
        font-weight: 400;
        line-height: 1.5;
        margin: 0;
    }
}