.heading-primary-box {}

.heading-primary {
    font-size: 19px;
    font-weight: 700;
    color: $color-primary;
    line-height: 1.4;
    text-align: center;
    letter-spacing: 2px;
    padding: 20px 0 0;

    @include mq('tablet-small') {
        font-size: 22px;
    }

    @include mq('tablet') {
        font-size: 34px;
        padding-top: 55px;
    }

    &--color {
        color: $color-white;
    }
}


//SECTION-INNER

.section-inner {
    padding: 25px 0;

    @include mq('tablet') {
        padding: 50px 0 65px;
    }

}